}

.header-overlay{
    background-color: rgba(199, 111, 207, 0.24);
    height: 250px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
   
}

.header-title{
    font-family: 'Cedarville Cursive', cursive;
    font-size: 70px;
    color: white;
    text-align: center;
    

}

.home{
    margin : 100px;
    }

.heading{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    margin: 20px auto 30px auto;
    text-align: center;
   
}

.button{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}


div{
    font-family: 'Quicksand', sans-serif;
}
.form{
    width: 300px;
    margin: 30px auto 30px auto;
}

.form-group{
    height: 50px !important;
}

input{
    border: none !important;
    border-radius: 0 !important;
    outline: none;
    border-bottom: 2px rgb(131, 130, 130) solid !important;
}

input:hover{
    background-color: rgba(0, 0, 0, 0.040);
}

input:focus, input.form-control:focus{
    border-bottom: 2px rgb(197, 91, 165) solid !important;
    box-shadow: none;
}


input:focus::-webkit-input-placeholder{
    opacity: 0;
    transition: 0.2s;
}


input:focus:-ms-input-placeholder{
    opacity: 0;
    transition: 0.2s;
}


input:focus::-ms-input-placeholder{
    opacity: 0;
    transition: 0.2s;
}


input:focus::placeholder{
    opacity: 0;
    transition: 0.2s;
}

label{
  opacity: 0;
  transition: 0.3s;
}


input:focus + label{
   opacity: 1;
   -webkit-transform: translateY(-60px) scale(0.70);
           transform: translateY(-60px) scale(0.70);
}

.btn{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    background-color: rgb(187, 113, 165);
}



